if (typeof window !== 'undefined' && window.Vue) {
  window.Vue.component('BaseFrame', component.exports)
}
export default {
  name: "BaseFrame",
  props: {
    hasTopBar: {
      type: Boolean,
      default: true,
    },
    hasDrawer: {
      type: Boolean,
      default: true,
    }
  },
  computed: {
    frameClasses() {
      return {
        "base-frame--has-top-bar": this.hasTopBar,
        "base-frame--has-drawer": this.drawer,
      }
    }
  }
}
